<template>
    <div class="tab-control">
        <div v-for="(item,index) in titles" :key="item"
            class="tab-control-item" :class="{active: index ===  currentIndex}"
            @click="itemClick(index)">
            <span>{{item}}</span>
        </div>
    </div>
</template>
<script>
export default {
    name:'TabControl',
    props:{
        titles:{
            type:Array,
            default() {
                return[]
            }
        }
    },
    data(){
        return {
            currentIndex:0,
        }
    }, 
    methods:{
        itemClick(index){
            this.currentIndex = index
            this.$emit('tabClick',index)
        }
    }
}
</script>
<style scoped>
   .tab-control{
       display: flex;
       text-align: center;
       font-size:15px;
       height: 40px;
       line-height:40px;
       background-color: #fff;
   }
   .tab-control-item{
       flex:1;
   }
   .active{
       color:var(--color-high-text);
   }
   .active span{
       border-bottom:2px solid var(--color-high-text);
       padding-bottom:5px;
   }
</style>